import RisksForm from './risks-form'
import RisksList from './risks-list'
import RisksDrawer from './risks-drawer'
import RisksActions from './risks-actions'
import Slider from '../../components/slider'

const RisksPlan = function () {
  const $refs = {
    slider: document.querySelector('#risk-slider')
  }

  const state = {
    views: {
      plan: 'risk-plan',
      search: 'risk-search'
    },
    emitter: null
  }

  return {
    init
  }

  /**
   * ðŸ”‘ Public Methods
   */
  function init ({ alias, emitter }) {
    const mountComponents = [
      'pesquisa',
      'validacao',
      'monitoramento',
      'reavaliacao'
    ].includes(alias)

    state.emitter = emitter
    state.alias = alias
    state.slider = Slider($refs.slider)

    state.emitter.on('search-done', showListView)
    state.emitter.on('search-return', showSearchView)
    state.emitter.on('drawer-edit', showDrawer)
    state.emitter.on('drawer-edit-save', appendActions)
    state.emitter.on('drawer-add', RisksList.append)

    if (mountComponents) {
      mount()
    }
  }

  /**
   * ðŸ”’ Private Methods
   */
  function mount () {
    const { emitter } = state

    state.slider.disableTransition()

    if (state.alias === 'pesquisa') {
      RisksForm.mount({ emitter })
      RisksList.mount({ emitter })
      RisksDrawer.mount({ emitter })

      state.slider.open(state.views.search)

      setTimeout(() =>
        state.slider.enableTransition(), 100
      )
    }

    if (state.alias === 'validacao') {
      RisksList.mount({ emitter })
      RisksDrawer.mount({ emitter })

      state.slider.open(state.views.plan)
    }

    if (state.alias === 'monitoramento') {
      const props = {
        editable: false,
        removeable: false,
        static: true
      }

      RisksList.mount({ emitter, props })
      RisksActions.mount({ emitter })

      state.slider.open(state.views.plan)
    }

    if (state.alias === 'reavaliacao') {
      const props = {
        removeable: false,
        static: true
      }

      RisksList.mount({ emitter, props })
      RisksDrawer.mount({ emitter })
      RisksActions.mount({ emitter })

      RisksDrawer.disabledRemove()
      state.slider.open(state.views.plan)
    }
  }

  function showListView (risks) {
    state.slider.open(state.views.plan)
    RisksList.populate(risks)
  }

  function showSearchView () {
    state.slider.open(state.views.search)
    RisksList.clear()
  }

  function showDrawer (options) {
    RisksDrawer.open(options)
  }

  function appendActions (risk) {
    if (state.alias === 'reavaliacao') {
      RisksActions.appendActions(risk.riscoDescricao)
    }
  }
}

export default RisksPlan()
