import RisksApi from './api/api.risks'
import { FormValidation } from '../../components/form-validation'
import Form from '../../components/form'

const SearchForm = new FormValidation([
  {
    element: '[xname=inptipoPlano]',
    errorMessage: 'Informe o tipo'
  }, {
    element: '[xname=inpunidade]',
    errorMessage: 'Informe a unidade'
  }, {
    element: '[xname=inpgerencia]',
    errorMessage: 'Informe a gerÃªncia'
  }, {
    element: '[xname=inpperiodoInicio]',
    errorMessage: 'Informe o inÃ­cio'
  }
])

const RisksForm = function () {
  const $refs = {
    trigger: document.querySelector('#btn-risks-search'),
    pane: document.querySelector('#risk-start-message'),
    year: document.querySelector('[xname=inpperiodoInicio]'),
    planType: document.querySelector('[xname=inptipoPlano]'),
    unit: document.querySelector('[xname=inpunidade]'),
    management: document.querySelector('[xname=inpgerencia]'),
    department: document.querySelector('[xname=inpdepartamento]'),
    assigneeArea: document.querySelector('[xname=inpareaResponsavel]'),
    areaName: document.querySelector('[xname=inptextoAreaResponsavel]')
  }

  const state = {
    emitter: null
  }

  return {
    mount
  }

  /**
   * ðŸ”‘ Public Methods
   */
  function mount ({ emitter }) {
    state.emitter = emitter

    $refs.trigger
      .addEventListener('click', handleStart)

    handlePlanType()
    mountDatePicker()
  }

  /**
   * ðŸ”’ Private Methods
   */
  function mountDatePicker () {
    $($refs.year)
      .datepicker({
        language: 'pt-BR',
        startView: 'months',
        minViewMode: 'months',
        format: 'M/yyyy'
      })
    $refs.year.autocomplete = 'off'
  }

  function handlePlanType () {
    const fields = [
      $refs.department,
      $refs.management,
      $refs.unit
    ]

    const params = {
      type: $refs.planType,
      management: $refs.management,
      unit: $refs.unit
    }

    $refs.planType.addEventListener(
      'change',
      () => onPlanTypeChange($refs.planType.value)
    )

    fields.forEach(field => {
      field.addEventListener(
        'change',
        () => setAssignee(params)
      )
    })

    onPlanTypeChange($refs.planType.value)
  }

  function onPlanTypeChange (type) {
    const changeEvent = new Event('change')
    const fields = [
      $refs.department,
      $refs.unit,
      $refs.management
    ]

    disableFields(fields)

    if (type === 'Departamento Regional') {
      $refs.department.disabled = false
      $refs.department.value = 'Sesi Bahia'
      $refs.department.dispatchEvent(changeEvent)

      Form.showField('departamento')
      Form.hideField('gerencia')
      Form.hideField('unidade')
    } else if (type === 'GerÃªncia de NegÃ³cio/Suporte') {
      $refs.management.disabled = false

      Form.showField('gerencia')
      Form.hideField('departamento')
      Form.hideField('unidade')
    } else if (type === 'GerÃªncia de Unidade') {
      $refs.unit.disabled = false

      Form.showField('unidade')
      Form.hideField('departamento')
      Form.hideField('gerencia')
    } else {
      Form.hideField('unidade')
      Form.hideField('departamento')
      Form.hideField('gerencia')
    }
  }

  function setAssignee (props) {
    const assignee = getAssigneeByPlanType({
      type: props.type.value,
      management: props.management.value,
      unit: props.unit.value
    })

    if (!assignee) {
      $refs.assigneeArea.value = ''
      $refs.areaName.value = ''
      return
    }

    $refs.assigneeArea.value = assignee.codarea
    $refs.areaName.value = assignee.areaName
  }

  function handleStart (e) {
    const { hasErrors } = validateForm()

    e.preventDefault()

    if (hasErrors) {
      return hasErrors
    }

    searchData()
  }

  function validateForm () {
    return SearchForm.validate()
  }

  function searchData () {
    const unitId = $refs.unit.value
    const year = $refs.unit.value

    addLoadingState()

    RisksApi
      .getRisks({ unitId, year })
      .then(handleSearchDone)
      .catch(handleSearchError)
  }

  function handleSearchDone (data) {
    state.emitter.emit('search-done', data)
    removeLoadingState()
  }

  function handleSearchError (err) {
    console.error(err)
    removeLoadingState()
  }

  function addLoadingState () {
    [$refs.trigger, $refs.pane]
      .forEach(el =>
        el.classList.add('-loading')
      )
  }

  function removeLoadingState () {
    [$refs.trigger, $refs.pane]
      .forEach(el =>
        el.classList.remove('-loading')
      )
  }
}

export default RisksForm()

/**
 * ðŸ’¡ Helpers
 */
function disableFields (fields) {
  fields.forEach(field => {
    field.disabled = true
  })
}

function getAssigneeByPlanType ({ type, management, unit }) {
  if (type === 'Departamento Regional') {
    return {
      codarea: process.env.ORQUESTRA_SUPER_CODAREA,
      areaName: getAreaTextFromField('departamento')
    }
  }

  if (type === 'GerÃªncia de NegÃ³cio/Suporte') {
    return {
      codarea: management,
      areaName: getAreaTextFromField('gerencia')
    }
  }

  if (type === 'GerÃªncia de Unidade') {
    return {
      codarea: unit,
      areaName: getAreaTextFromField('unidade')
    }
  }

  return null
}

function getAreaTextFromField (fieldId) {
  const field = document.querySelector(`[xname=inp${fieldId}]`)
  const fieldType = field.tagName.toLocaleLowerCase()

  if (fieldType === 'input') {
    return field.value
  }

  if (fieldType === 'select') {
    return field.querySelector('option:checked').textContent
  }

  return null
}
