function mount (group, context) {
  const element = context.querySelector(group.element)
  const errorMessage = group.errorMessage

  if (!element) {
    return console.error(`[Form Validation] element not found for selector: ${group.element}`)
  }

  setTriggers(element, errorMessage)
}

function setTriggers (element, errorMessage) {
  // Utilizado jQuery para corrigir o nÃ£o disparo de
  // eventos `change` em campos do tipo `data` do Orquestra
  // que fazem uso do componente Bootstrap Datepicker.
  $(element).on('change', function (e) {
    handleChange.call(this, e, errorMessage)
  })
}

function handleChange (e, errorMessage) {
  validateField(this, errorMessage)
}

function validateField (field, errorMessage, silent) {
  if (field.disabled) {
    if (!silent) removeError(field)
    return false
  }
  if (field.value === '') {
    if (!silent) addError(field, errorMessage)
    return true
  } else {
    if (!silent) removeError(field)
    return false
  }
}

function validateForm (groups, silent, context) {
  let hasErrors = false

  const result = groups.map(group => {
    const element = context.querySelector(group.element)
    const errorMessage = group.errorMessage
    const validation = {
      result: validateField(element, errorMessage, silent),
      error: null
    }

    if (!validation.result) {
      return validation
    }

    hasErrors = true
    validation.error = errorMessage

    return validation
  })

  const errors = result.filter(({ error }) => error !== null)

  return {
    hasErrors,
    errors
  }
}

const getFormHelpElement = message =>
  `<p class="form-help">${message}</p>`

/**
 * Public API
 */
export class FormValidation {
  constructor (config, context = document) {
    this.groups = config
    this.context = context

    if (!Array.isArray(config)) {
      return console.error('[Form Validation] config parameter must by a array with a element selector, error message and callback (optional)')
    }

    this.groups.forEach(group => mount(group, context))
  }

  validate (silent = false) {
    return validateForm(this.groups, silent, this.context)
  }
}

export function addError (element, errorMessage) {
  const formGroup = element.closest('.form-group')

  if (!formGroup) {
    return console.error('[Form Validation] the element must be inside a .form-group container')
  }

  const formHelp = formGroup.querySelector('.form-help')

  formGroup.classList.add('-error')

  if (!formHelp) {
    const formHelp = getFormHelpElement(errorMessage)
    formGroup.insertAdjacentHTML('beforeend', formHelp)
  } else {
    formHelp.textContent = errorMessage
  }
}

export function removeError (element) {
  const formGroup = element.closest('.form-group')
  const formHelp = formGroup.querySelector('.form-help')

  if (!formGroup) {
    return console.error('[Form Validation] the element must be inside a .form-group container')
  }

  if (formHelp) {
    formHelp.remove()
  }

  formGroup.classList.remove('-error')
}
