import Drawer from '../../components/drawer'
import ButtonRadio from '../../components/button-radio'

const RisksDrawer = function () {
  const $refs = {
    drawer: document.querySelector('#risk-drawer'),
    probability: document.querySelector('[name=riscoProbabilidade]'),
    impact: document.querySelector('[name=riscoImpacto]'),
    level: document.querySelector('[name=riscoNivel]'),
    cells: document.querySelectorAll('#risk-drawer .matrix-cell-inner')
  }

  const state = {
    emitter: null
  }

  return {
    mount,
    open,
    disabledRemove
  }

  /**
   * ðŸ”‘ Public Methods
   */
  function mount ({ emitter }) {
    state.emitter = emitter

    state.drawer = Drawer($refs.drawer, {
      onConfirm,
      title: 'Adicionar Item',
      confirmText: 'Adicionar'
    })

    ButtonRadio('[data-btn-radio]')
    validateFill()
    addMatrixTriggers()

    $refs.drawer
      .querySelectorAll('[data-btn-hint]')
      .forEach(addHintTriggers)
  }

  function open (options) {
    const { data, ...props } = options

    if (data) {
      state.drawer.populate(data)
    }

    onMatrixChange()

    state.drawer.open({
      title: 'Editar Item',
      confirmText: 'Salvar',
      enableRemove: true,
      ...props
    })
  }

  function disabledRemove () {
    $refs.drawer
      .querySelectorAll('[data-drawer-remove]')
      .forEach(btn => btn.remove())
  }

  /**
   * ðŸ”’ Private Methods
   */
  function onConfirm (row, data) {
    state.emitter.emit('drawer-add', data)
  }

  function validateFill () {
    $refs.btnConfirm = $refs.drawer
      .querySelector('[data-drawer-confirm]')

    $refs.fields = [
      ...$refs.drawer.querySelectorAll('[name]')
    ]

    $refs.btnConfirm.disabled = true

    $refs.fields
      .forEach(field =>
        field.addEventListener('change', onFormFilling)
      )
  }

  function onFormFilling () {
    const isFilled = isFormFilled()

    if (isFilled) {
      $refs.btnConfirm.disabled = false
    } else {
      $refs.btnConfirm.disabled = true
    }
  }

  function isFormFilled () {
    return !$refs.fields
      .some(field => !field.value)
  }

  function addHintTriggers (fieldContainer) {
    const buttons = fieldContainer.querySelectorAll('.btn-radio')
    const field = fieldContainer.querySelector('select')
    const hints = [
      ...fieldContainer
        .nextElementSibling
        .querySelectorAll('[data-hint]')
    ]

    field.addEventListener('change', e => {
      const option = e.target.value

      hideAllHints(hints)

      if (option) {
        changeHintDisplay({
          option,
          hints,
          display: 'initial'
        })
      }
    })

    buttons.forEach(btn => {
      btn.addEventListener('mouseenter', e => {
        if (!field.value) {
          const option = e.target.dataset.option
          changeHintDisplay({
            option,
            hints,
            display: 'initial'
          })
        }
      })
      btn.addEventListener('mouseleave', e => {
        if (!field.value) {
          const option = e.target.dataset.option
          changeHintDisplay({
            option,
            hints,
            display: 'none'
          })
        }
      })
    })
  }

  function changeHintDisplay (params) {
    const {
      option,
      hints,
      display
    } = params

    const hint = findHint(hints, option)

    if (hint) {
      hint.style.display = display
    }
  }

  function hideAllHints (hints) {
    hints.forEach(hint => {
      hint.style.display = 'none'
    })
  }

  function findHint (hints, option) {
    return hints
      .find(hint => hint.dataset.hint === option)
  }

  function addMatrixTriggers () {
    [
      $refs.probability,
      $refs.impact
    ].forEach(field =>
      field.addEventListener('change', onMatrixChange)
    )
  }

  function onMatrixChange () {
    const probability = getMatrixValueFromLabel(
      $refs.probability.value
    )
    const impact = getMatrixValueFromLabel(
      $refs.impact.value
    )

    clearMatrixState()

    if (!probability || !impact) {
      return
    }

    const level = probability * impact
    const pos = probability + (impact - 1) * 3

    $refs.level.value = level
    $refs.cells[pos - 1].dataset.value = level
  }

  function clearMatrixState () {
    $refs.cells.forEach(cell => {
      cell.dataset.value = ''
    })
  }
}

export default RisksDrawer()

/**
 * ðŸ’¡ Helper Functions
 */

function getMatrixValueFromLabel (level) {
  level = level.toLowerCase()

  if (level === 'baixo' || level === 'baixa') {
    return 1
  }

  if (level === 'mÃ©dio' || level === 'mÃ©dia') {
    return 2
  }

  if (level === 'alto' || level === 'alta') {
    return 3
  }

  return null
}
